.add-cell{
    position: relative;
    opacity: 0;
    transition: opacity 0.3s ease-in 0.1s;
    margin: 10px;
}
.add-cell:hover{
    opacity: 1.0;
}
.add-cell .divider {
    position: absolute;
    top:50%;
    right: 2.5%;
    left: 2.5%;
    border-bottom: 1px solid gray;
    width: 90%;
    z-index: -1;
}

.add-cell .add-buttons {
    display: flex;
    justify-content: center;
}

.add-cell .add-buttons button {
    margin: 0 40px;
}

.add-cell.force-visible {
    opacity: 1;
}