.progress-wrapper{
    height: 100%;
    flex-grow: 1;
    background-color: white;
}


.progress-cover {
    height: 100%;
    flex-grow: 1;
    background-color: white;
    display: flex;
    align-items: center;
    padding-left: 10%;
    padding-right: 10%;
    animation: fadeIn 0.5s;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    };
}
